@import 'scss/variables';
@import 'scss/fonts';
@import 'scss/about';
@import 'scss/footer';
@import 'scss/header';
@import 'scss/select';
@import 'scss/swap';

* {
    box-sizing: border-box;
}

html, body {
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Suisse Intl';
    font-size: 14px;
}

.container {
    width: 100%;
    max-width: 1200px;
    padding: 0 15px;
    margin: 0 auto;
}

a {
    color: black;
    text-decoration: none;
}

p {
    color: $dark-grey;
    line-height: 1.5;
    margin: 0;
}

h2 {
    font-size: 30px;
    margin-top: 0;
    margin-bottom: 20px;

    @media (max-width: $max-md) {
        font-size: 24px;
    }
}
