.about {
    text-align: center;
    margin-top: 150px;

    & > div {
        padding: 70px 0;
    }
    
    .info {
        background-color: $light-grey;
    }
    
    .token {
        svg {
            height: 40px;
            margin-right: 20px;
            vertical-align: middle;
        }
    }

    p {
        max-width: 540px;
        margin: 0 auto;
    }

    @media(max-width: $max-md) {
        margin-top: 70px;
    }
}