.swap {
    width: 460px;
    margin: 0 auto;

    .tabs {
        background-color: $light-grey;
        display: inline-flex;
        margin: 60px 0 40px;
        border-radius: 18.5px;
        position: relative;
        left: 50%;
        transform: translateX(-50%);
        cursor: pointer;

        .indicator {
            background-color: $orange;
            height: 100%;
            width: 50%;
            position: absolute;
            border-radius: 18.5px;
            transition: all 0.2s ease-in-out;

            &.left {
                left: 0;
            }
            &.right {
                left: 50%;
            }
        }

        .item {
            padding: 10px 30px;
            z-index: 1;
            transition: color 0.2s ease-in-out;

            &.active {
                color: white;
            }
        }
    }

    .bg {
        display: flex;
        justify-content: space-between;
        background-color: $light-grey;
        width: 100%;
        padding: 20px 20px 20px 40px;
        border-radius: 47.5px;

        & + .bg {
            margin-top: 20px;
        }

        .input-wrapper {
            display: flex;
            flex-direction: column;
        }

        .input {
            background-color: $light-grey;
            font-size: 24px;
            outline: none;
            border: none;
            margin-top: 8px;
        }

        .select {
            display: flex;
            align-items: center;
        }
    }

    .bg:not(.full) {
        .input {
            width: 200px;
        }
    }

    .arrow {
        display: flex;
        justify-content: center;
        margin: 20px 0;
    }

    .fee {
        text-align: center;
        margin-top: 15px;
    }

    .btcaddress {
        width: 55%;
        text-align: center;
        margin: 25px 0 25px 23%;
        border: none;
        outline: none;
        font-family: Helvetica;
        font-size: 24px;
        border-bottom: 1px solid;
    }

    .button {
        font-size: 16px;
        background-color: $orange;
        color: #fff;
        width: 250px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0 auto;
        margin-top: 40px;
        height: 50px;
        border: none;
        border-radius: 25px;
        outline: none;
        cursor: pointer;
    }

    .error {
        color: #eb4034;
        text-align: center;
        margin-top: 20px;
    }

    @media(max-width: $max-sm) {
        width: 100%;
    }
}
