.header {
    .warning {
        color: #fff;
        background-color: $orange;
        text-align: center;
        padding: 10px 0px;
    }

    .container {
        padding: 30px 0;
        display: flex;
        justify-content: space-between;
        align-items: center;

        .logo {
            height: 40px;
        }

        button {
            color: $orange;
            background-color: #fff;
            border: 2px solid $orange;
            padding: 0 30px;
            height: 40px;
            border-radius: 20px;
            text-align: center;
            outline: none;
            cursor: pointer;
        }
    }
}
